import React, { useState } from "react";
import Footer from "@/components/footer/page";
import Header from "@/components/header/page";
import Sidebar from "@/components/sidebar/page";

export default function AddNewProduct() {
  const [productDetails, setProductDetails] = useState({
    name: "",
    description: "",
    category: "",
    subcategory: "",
    sku: "",
    price: "",
    discountPrice: "",
    tax: "",
    currency: "USD",
    stockQuantity: "",
    stockStatus: "In Stock",
    minOrderQuantity: "",
    images: [],
    videoUrl: "",
    slug: "",
    sizes: [],
    colors: [],
    attributes: {
      material: "",
      weight: "",
      dimensions: "",
    },
  });

  const handleChange = (
    e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement | HTMLSelectElement>
  ) => {
    const { name, value } = e.target;
    setProductDetails((prev) => ({
      ...prev,
      [name]: value,
    }));
  };

  const handleMultiSelectChange = (name: string, value: string) => {
    setProductDetails((prev) => {
      const currentValues = prev[name as keyof typeof productDetails] as string[];
      return {
        ...prev,
        [name]: currentValues.includes(value)
          ? currentValues.filter((item) => item !== value)
          : [...currentValues, value],
      };
    });
  };

  const handleFileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (e.target.files) {
      setProductDetails((prev) => ({
        ...prev,
        images: Array.from(e.target.files),
      }));
    }
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    console.log(productDetails);
  };

  return (
    <div className="flex flex-col min-h-screen">
      <div className="flex flex-grow">
        <Sidebar />
        <div className="flex flex-col flex-grow">
          <Header />

          <section className="p-6 bg-gray-50 rounded-lg shadow-lg">
            <h2 className="text-2xl font-bold text-gray-900 mb-4 text-center">
              Add New Product
            </h2>

            <form onSubmit={handleSubmit} className="space-y-6">
              {/* Basic Product Details */}
              <div className="grid grid-cols-1 gap-8 md:grid-cols-2">
                {/* Existing Fields */}
                {/* Add new sections for sizes, colors, and attributes */}
                <div>
                  <label className="text-lg font-semibold text-gray-700">
                    Sizes
                  </label>
                  <div className="flex flex-wrap gap-2 mt-2">
                    {["Small", "Medium", "Large", "XL", "XXL"].map((size) => (
                      <label key={size} className="flex items-center space-x-2">
                        <input
                          type="checkbox"
                          value={size}
                          checked={productDetails.sizes.includes(size)}
                          onChange={() => handleMultiSelectChange("sizes", size)}
                          className="form-checkbox"
                        />
                        <span>{size}</span>
                      </label>
                    ))}
                  </div>
                </div>

                <div>
                  <label className="text-lg font-semibold text-gray-700">
                    Colors
                  </label>
                  <div className="flex flex-wrap gap-2 mt-2">
                    {["Red", "Blue", "Green", "Black", "White"].map((color) => (
                      <label key={color} className="flex items-center space-x-2">
                        <input
                          type="checkbox"
                          value={color}
                          checked={productDetails.colors.includes(color)}
                          onChange={() => handleMultiSelectChange("colors", color)}
                          className="form-checkbox"
                        />
                        <span>{color}</span>
                      </label>
                    ))}
                  </div>
                </div>
              </div>

              {/* Attributes */}
              <div>
                <h3 className="text-lg font-semibold text-gray-700">
                  Additional Attributes
                </h3>
                <div className="grid grid-cols-1 gap-4 md:grid-cols-3 mt-4">
                  <div>
                    <label className="text-lg font-semibold text-gray-700">
                      Material
                    </label>
                    <input
                      type="text"
                      name="material"
                      value={productDetails.attributes.material}
                      onChange={(e) =>
                        setProductDetails((prev) => ({
                          ...prev,
                          attributes: { ...prev.attributes, material: e.target.value },
                        }))
                      }
                      className="mt-2 w-full px-4 py-2 rounded-xl border border-gray-300 focus:outline-none focus:ring-2 focus:ring-indigo-500"
                      placeholder="Enter material"
                    />
                  </div>
                  <div>
                    <label className="text-lg font-semibold text-gray-700">
                      Weight
                    </label>
                    <input
                      type="text"
                      name="weight"
                      value={productDetails.attributes.weight}
                      onChange={(e) =>
                        setProductDetails((prev) => ({
                          ...prev,
                          attributes: { ...prev.attributes, weight: e.target.value },
                        }))
                      }
                      className="mt-2 w-full px-4 py-2 rounded-xl border border-gray-300 focus:outline-none focus:ring-2 focus:ring-indigo-500"
                      placeholder="Enter weight (e.g., 1kg)"
                    />
                  </div>
                  <div>
                    <label className="text-lg font-semibold text-gray-700">
                      Dimensions
                    </label>
                    <input
                      type="text"
                      name="dimensions"
                      value={productDetails.attributes.dimensions}
                      onChange={(e) =>
                        setProductDetails((prev) => ({
                          ...prev,
                          attributes: { ...prev.attributes, dimensions: e.target.value },
                        }))
                      }
                      className="mt-2 w-full px-4 py-2 rounded-xl border border-gray-300 focus:outline-none focus:ring-2 focus:ring-indigo-500"
                      placeholder="Enter dimensions (e.g., 10x10x5 cm)"
                    />
                  </div>
                </div>
              </div>

              {/* Submit Button */}
              <div className="mt-8 flex justify-center">
                <button
                  type="submit"
                  className="px-6 py-3 bg-indigo-600 text-white font-semibold rounded-xl hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-indigo-500"
                >
                  Add Product
                </button>
              </div>
            </form>
          </section>
        </div>
      </div>
      <Footer />
    </div>
  );
}
